import React from 'react';
import welcomeStyle from './Welcome.module.css';

import { Input } from 'antd';
import { DownloadOutlined } from '@ant-design/icons';

import img_sc from '../../assets/sc.jpg';

function Item(props) {
    const { img, title, author } = props;

    return (
        <div className={welcomeStyle.item}>
            <div className={welcomeStyle.itemImg}>
                <img alt='No Preview' src={img} />
            </div>
            <div className={welcomeStyle.itemDesp}>
                <div className={welcomeStyle.videoTitle}>{title}</div>
                <div className={welcomeStyle.videoAuthor}>
                    <span>{author}</span>
                    <DownloadOutlined twoToneColor='#eb2f96' />
                </div>
            </div>
        </div>
    );
}

function Welcome(props) {
    const handleSearch = (key) => {
        console.log(key);
    };

    return (
        <div className={welcomeStyle.welcome}>
            <section id={welcomeStyle.search}>
                <Input.Search placeholder="input search text" onSearch={handleSearch} enterButton allowClear maxLength={100} />
            </section>
            <section id={welcomeStyle.list}>
                <Item title={'斗鱼直播20天可以领m4竞技战魂 永久'} author={'作者昵称'} img={img_sc} />
            </section>
        </div>
    );
}

export default Welcome;